import React, { useEffect } from "react";
import { useState } from "react";
import TodoItem from "./TodoItem";
import TodoForm from "./TodoForm";
import axios from "axios";
// import useFetch from "react-fetch-hook";

export default function Todos() {
  const [todos, setTodos] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      const result = await axios(
        "https://mysterious-basin-95318.herokuapp.com/todo"
      );
      setTodos(result.data.data);
    };

    fetchData();
    setIsLoading(false);
  }, []);

  const addTodo = async (todo) => {
    await axios.post("https://mysterious-basin-95318.herokuapp.com/todo", todo);
    setTodos([todo, ...todos]);
  };

  const checkTodo = async (id) => {
    //láº¥y ra todo item cáº§n thay Äá»i tráº¡ng thÃ¡i. Tháº±ng todo táº¡o má»i cÃ³ Äá»a chá» Ã´ nhá» náº±m trong arry todos nÃªn nÃ³ sáº½ thay Äá»i tráº¡ng thÃ¡i cá»§a todos
    let todo = todos.find((element) => element.id === id);
    todo.status = todo.status === "check" ? "not check" : "check";

    axios.put(`https://mysterious-basin-95318.herokuapp.com/todo/${id}`, todo);

    setTodos([...todos]);
  };

  const deleteTodo = async (id) => {
    await axios.delete(
      `https://mysterious-basin-95318.herokuapp.com/todo/${id}`
    );
    setTodos(todos.filter((todo) => todo.id !== id));
  };

  return isLoading ? (
    <div className="loader">
      <p className="loader__text">Loading...</p>
    </div>
  ) : (
    <div className="todo-list">
      <TodoForm addTodo={addTodo} />
      <ul>
        {todos.map((todo) => (
          <TodoItem
            todo={todo}
            key={todo.id}
            checkTodo={checkTodo}
            deleteTodo={deleteTodo}
          />
        ))}
      </ul>
    </div>
  );
}
